@import url(https://fonts.googleapis.com/css2?family=Lato:ital@0;1&family=Nunito&display=swap);
body {
   margin: 0;
   font-family: 'Lato', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}

code {
   font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
   monospace;
}

.titolo{font-family:'Nunito', sans-serif;text-align:center;letter-spacing:3px}.titolo h1{margin-bottom:0}.subtitolo{font-family:'Lato', sans-serif;margin-top:0;letter-spacing:normal;letter-spacing:initial}.serĉilo{text-align:center}.serĉiTeksto{border:#343A40 solid 2px;padding:7px;width:177px}.serĉiButono{margin-left:10px;padding:7px;border:#343A40 solid 2px;background-color:transparent}.enhavo{max-width:700px;margin:0 auto}input{font-family:'Lato', sans-serif}a{text-decoration:none;text-shadow:0 0 2px #343A40}a:link,a:visited{color:#343A40}a:hover{text-shadow:0 0 4px #343A40}

.peto{margin-bottom:30px}.rezulto{margin-bottom:15px}.rezulto-vorto{font-family:'Nunito', sans-serif;font-size:2em;margin-right:8px}

h2.vorto{font-family:'Nunito', sans-serif;font-size:3em;letter-spacing:2px;margin-bottom:3px;margin-right:10px;display:inline}.streko{height:3px;background-color:#343A40}.vorttipo{font-style:italic;margin-top:0}

.kanvaso{position:relative;top:5px}

